<template>
  <div class="container">
    <div class="login-form">
      <div v-if="errors.length > 0" class="login-errors">
        <p class="error" v-for="err in errors" :key="err">{{ err }}</p>
      </div>
      <form @submit.prevent="">
        <div class="form-group">
          <label for="username">Username</label>
          <input
            type="text"
            id="username"
            autocomplete="username"
            v-model="username"
            required
          />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input
            type="password"
            id="password"
            autocomplete="new-password"
            v-model="password"
            required
          />
        </div>
        <button type="submit" @click="login">Login</button>
      </form>
    </div>
    <h1 @click="logout">logout</h1>
  </div>
</template>

<script>
import axios from "axios";
const url = "api/users/";

export default {
  data() {
    return {
      username: "",
      password: "",
      registerMsg: "",
      errors: [],
    };
  },
  methods: {
    async login() {
      // Send login request to server
      const data = {
        username: this.username,
        password: this.password,
      };
      await axios
        .post(`${url}login`, data)
        .then((response) => {
          // Login successful
          if (response) {
            console.log("success log in");
            this.$router.push({ path: "/" });
          }
        })
        .catch((error) => {
          // Login failed
          if (error) {
            console.log(error);
            this.errors.push("username or passwords are incorrect!");
          }
        });
    },
    logout() {
      axios
        .get(`${url}logout`)
        .then((response) => console.log(response))
        .catch((err) => console.log(err));
    },
  },
};
</script>

