  background-color: rgb(255 0 0 / 46%);
  color: black;
  margin: 0;
  padding: 12px 0;
}
.error:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}

.form-group {
  margin-bottom: 1rem;
}
label {
  display: block;
  margin-bottom: 0.5rem;
}
