  border: 1px solid #ccc;
  margin: 10px 0 30px;
}
.error {
  background-color: rgb(255 0 0 / 46%);
  color: black;
  margin: 0;
  padding: 12px 0;
}
.error:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}
.form-group {
  margin-bottom: 1rem;
}
label {
  display: block;
  margin-bottom: 0.5rem;
}
input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  max-width: 772px;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  font-size: 1rem;
}
button[type="submit"] {
  padding: 0.5rem;
  border: none;
  border-radius: 0.25rem;
  background-color: #007bff;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
}
</style>
