<template>
  <div class="todo-container">
    <h1>Todo</h1>
    <div class="add-todo">
      <label for="todo">Add a new todo</label>
      <input type="text" id="todo" v-model="content" @keyup.enter="postTodo" />
      <button class="add" @click="postTodo">Add!</button>
    </div>
    <div
      class="todos"
      v-for="todo in todos"
      :key="todo._id"
      @click="getSpecificTodo(todo._id)"
    >
      <span class="date">{{ todo.date }}</span>
      <p class="content">{{ todo.content }}</p>
      <div class="buttons">
        <button class="delete" @click.stop="deleteTodo(todo._id)">
          Delete
        </button>
        <button class="update" @click.stop="getSpecificTodo(todo._id)">
          Update
        </button>
      </div>
    </div>
    <div v-if="updateShow" class="update-layout">
      <div class="update">
        <label for="update-todo">Update a todo</label>
        <input
          type="text"
          id="update-todo"
          v-model="updateTodo.content"
          @keyup.enter="
            update();
            updateShow = false;
          "
        />
        <button
          class="add"
          @click.stop="
            update();
            updateShow = false;
          "
        >
          Update!
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import todoServices from "@/todoServices";

export default {
  name: "TodoComponent",
  data() {
    return {
      todos: [],
      error: "",
      content: "",
      updateShow: false,
      updateS: false,
      updateTodo: {},
    };
  },
  watch: {
    updateS(val) {
      this.updateShow = val;
    },
  },
  async created() {
    // getting data from db after creating page
    // console.log(await todoServices.getTodos());
    let wholeData = await todoServices.getTodos();
    this.todos = wholeData.data;
  },
  methods: {
    async postTodo() {
      // it will take its param from (this.)
      if (this.content != "") {
        await todoServices.postTodo(this.content);
      }

      // clear the input
      this.content = "";

      // add the posted todo to this.todos
      let wholeData = await todoServices.getTodos();
      this.todos = wholeData.data;
    },
    async deleteTodo(id) {
      await todoServices
        .deleteTodo(id)
        .then((response) => console.log(response + " deleted!"))
        .catch((error) => console.log(error));

      let wholeData = await todoServices.getTodos();
      this.todos = wholeData.data;
    },
    // get one to update it
    async getSpecificTodo(id) {
      await todoServices
        .getToUpdate(id)
        .then((response) => {
          if (typeof response == "object") {
            this.updateShow = true;
            this.updateTodo = response;
          }
          console.log(response);
        })
        .catch((error) => console.log(error));
    },
    async update() {
      await todoServices.updateTodo(
        this.updateTodo._id,
        this.updateTodo.content
      );

      let wholeData = await todoServices.getTodos();
      this.todos = wholeData.data;
    },
  },
};
</script>

